










import Vue from 'vue';
import List from '@/components/List.vue';

export default Vue.extend({
  name: 'Board',
  components: {
    List,
  },
  computed: {
    board() {
      return this.$store.state.board;
    },
  },
  async mounted() {
    await this.$store.dispatch('getBoard', this.$route.params.board_id);
  },
});
